<template>
  <div>
    <el-row type="flex" justify="center" class="row" align="middle">
      <el-col :span="5">
        allow student to choose
        <el-switch
          @change="changeOpen"
          v-model="control.open">
        </el-switch>
      </el-col>
      <el-col :span="5">
        allow student to see
        <el-switch
          @change="changeSeen"
          v-model="control.seen">
        </el-switch>
      </el-col>
      <el-col :span="5">
        <el-button type="primary" @click="add">Add</el-button>
      </el-col>
      <el-col :span="5">
        <el-button type="primary" @click="downloadStudent">Student Course</el-button>
      </el-col>
      <el-col :span="5">
        <el-button type="primary" @click="downloadCourse">Course</el-button>
      </el-col>
    </el-row>

    <el-row type="flex" justify="space-around" class="row">
      <el-col :span="11">
        Monday
        <el-table
          border
          @row-click="select"
          :data="courses.mon">
          <el-table-column
            prop="title"
            width="160"
            label="Name">
          </el-table-column>
          <el-table-column
            prop="grade"
            label="Grade">
          </el-table-column>
          <el-table-column
            prop="fee"
            label="Fee">
          </el-table-column>
          <el-table-column
            prop="teacherName"
            label="Teacher">
          </el-table-column>
          <el-table-column
            prop="submitted"
            label="Submitted">
          </el-table-column>
        </el-table>
      </el-col>
      <el-col :span="11">
        Tuesday
        <el-table
          border
          @row-click="select"
          :data="courses.tue">
          <el-table-column
            prop="title"
            width="160"
            label="Name">
          </el-table-column>
          <el-table-column
            prop="grade"
            label="Grade">
          </el-table-column>
          <el-table-column
            prop="fee"
            label="Fee">
          </el-table-column>
          <el-table-column
            prop="teacherName"
            label="Teacher">
          </el-table-column>
          <el-table-column
            prop="submitted"
            label="Submitted">
          </el-table-column>
        </el-table>
      </el-col>
    </el-row>

    <el-row type="flex" justify="space-around" class="row">
      <el-col :span="11">
        Wednesday
        <el-table
          border
          @row-click="select"
          :data="courses.wed">
          <el-table-column
            prop="title"
            width="160"
            label="Name">
          </el-table-column>
          <el-table-column
            prop="grade"
            label="Grade">
          </el-table-column>
          <el-table-column
            prop="fee"
            label="Fee">
          </el-table-column>
          <el-table-column
            prop="teacherName"
            label="Teacher">
          </el-table-column>
          <el-table-column
            prop="submitted"
            label="Submitted">
          </el-table-column>
        </el-table>
      </el-col>
      <el-col :span="11">
        Thursday
        <el-table
          border
          @row-click="select"
          :data="courses.thu">
          <el-table-column
            prop="title"
            width="160"
            label="Name">
          </el-table-column>
          <el-table-column
            prop="grade"
            label="Grade">
          </el-table-column>
          <el-table-column
            prop="fee"
            label="Fee">
          </el-table-column>
          <el-table-column
            prop="teacherName"
            label="Teacher">
          </el-table-column>
          <el-table-column
            prop="submitted"
            label="Submitted">
          </el-table-column>
        </el-table>
      </el-col>
    </el-row>
  </div>
</template>

<script>
import {getAllCourses, downloadStudentCourse, downloadCourse} from 'api/asa'
import {getCurrentControl, updateControl} from 'api/control'
import {
  mapMutations
} from 'vuex'
import {userValidation} from 'assets/js/mixin'

export default {
  name: 'HelloWorld',
  data () {
    return {
      courses: {},
      control: {}
    }
  },
  created () {
    getCurrentControl().then((res) => {
      this.$ls.set('control', res)
      this.control = res
    })
    getAllCourses().then((res) => {
      this.courses = res
    })
  },
  mixins: [userValidation],
  methods: {
    ...mapMutations({
      setCurrentASA: 'SET_CURRENT_ASA'
    }),
    add() {
      console.log('add')
      this.$router.push('/admin/asa/add')
    },
    changeOpen(value) {
      updateControl(this.control.objectId, {open: value}).then((res) => {
        if (JSON.parse(res).code !== 101) {
          this.$notify({
            message: '更新成功'
          })
        }
      })
    },
    changeSeen(value) {
      updateControl(this.control.objectId, {seen: value}).then((res) => {
        if (JSON.parse(res).code !== 101) {
          this.$notify({
            message: '更新成功'
          })
        }
      })
    },
    select (obj) {
      this.setCurrentASA(obj)
      this.$router.push('/admin/asa/detail')
    },
    downloadStudent() {
      downloadStudentCourse({
        email: 'rainbowhuang@bcis.net.cn',
        subject: 'Student Course',
        body: 'Student Course',
        filename: 'Student Course.csv'
      }).then((res) => {
        this.$notify({
          message: '发送成功'
        })
      })
    },
    downloadCourse() {
      downloadCourse({
        email: 'rainbowhuang@bcis.net.cn',
        subject: 'ASA Course',
        body: 'ASA Course',
        filename: 'ASA Course.csv'
      }).then((res) => {
        this.$notify({
          message: '发送成功'
        })
      })
    }
  }
}
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style lang="stylus" scoped>
  @import "~variables"
  .row
    margin-bottom $small-margin
</style>



